import { useMemo, useRef, DependencyList } from 'react';

/**
 *  combo  options.timeout  click 
 * options.times  onFire
 */
export function useClickCombo(
  options: {
    timeout: number
    times: number
    onFire: () => void
  },
  deps: DependencyList,
): {
  click(): void
  reset(): void
} {
  /**
   *  useMemo  combo  ID  combo 
   *  combo  click  onFire 
   */
  const comboIdRef = useRef(0)

  return useMemo(
    () => {
      comboIdRef.current += 1
      const comboId = comboIdRef.current

      let timer: number | undefined
      let count = 0

      const reset = () => {
        count = 0
      }

      const click = () => {
        if (!timer) {
          timer = window.setTimeout(reset, options.timeout)
        }

        count += 1

        if (
          count === options.times &&
          comboId === comboIdRef.current
        ) {
          options.onFire?.()
        }
      }

      return { click, reset }
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    deps,
  );
}
